<template>
    <div><p class="headline primary--text">Редактировать</p>

        <p >Введите email нового пользователя,
            логин и пароль будут автоматически сгенерированы и высланы на указанный почтовый ящик.</p>

        <v-text-field
                class="mt-4"
                input="email"
                :error-messages="errors.collect('email')"
                v-validate="'required'"
                data-vv-name="email"
                required
                v-model="userDto.email"
                label="Email">
        </v-text-field>

        <v-btn class="mt-5 right" color="primary" @click="submit">Сохранить</v-btn>

    </div>
</template>

<script>
    import axios from 'axios'
    import VuePassword from "vue-password/src/components/VuePassword";

    export default {
        components: {VuePassword},
        data: () => ({
            userDto: {
                name: 'name',
                surname: 'surname',
                login: 'login',
                id: null,
                role: 'role',
                password: 'password',
                email: 'email'
            },
            dictionary: {
                custom: {
                    email: {
                        required: () => 'Введите email',
                        valid: () => 'Введите email'
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    let headers = {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json'
                    };
                    axios.post(`/users/api/add`, this.userDto, headers)
                        .then(response => {
                            this.userDto = response.data;
                            if (this.newsDto.id != null) {
                                this.$router.push('/users');
                            }
                        })
                        .catch(e => {
                            this.errors.push(e)
                        })
                }
            }
        }, created() {
            axios.get(`/users/api`)
                .then(response => {
                    this.userDt = response.data
                })
                .catch(e => {
                    this.errors.push(e)
                })
        }
    }
</script>

