<template>
    <div>
        <v-btn color="blue darken-3" to="/courses/add"
               class="ml-0 mt-2" dark>Добавить
        </v-btn>


        <v-expansion-panel focusable expand class="mt-3">
            <v-expansion-panel-content v-for="(item, index) in courses" :key="index">

                <div slot="header">
                    <v-layout row class="right">
                        <v-flex xs12 sm3 class="mr-3 ml-3">
                            <v-btn @click="deleteCourse(index, item.id)" flat icon >
                                <v-icon color="grey darken-4">delete</v-icon>
                            </v-btn>
                        </v-flex>
                        <v-flex xs12 sm3>
                            <v-btn v-bind:to="getEdit(item.id)" flat icon >
                                <v-icon color="blue darken-2">edit</v-icon>
                            </v-btn>
                        </v-flex>
                    </v-layout>

                    <h5 class="md-card-header">{{item.name}}</h5>
                    <p class="md-subheader">{{item.prof}}</p>
                </div>

                <v-card>
                    <v-card-text class="grey lighten-3">
                        {{item.info}}
                    </v-card-text>
                </v-card>

            </v-expansion-panel-content>
        </v-expansion-panel>

    </div>
</template>

<script>
    import axios from 'axios';
    export default {

        name: "Courses",
        data() {
            return {
                courses: [{
                    id: 2,
                    name: "Course name",
                    prof: "Name surname",
                    info: "Course info"
                }, {
                    id: 2,
                    name: "Course name",
                    prof: "Name surname",
                    info: "Course info"
                }, {
                    id: 3,
                    name: "Course name",
                    prof: "Name surname",
                    info: "Course info"
                }, {
                    id: 4,
                    name: "Course name",
                    prof: "Name surname",
                    info: "Course info"
                }
                ]
            }
        },
        created() {
            this.getAllCourses()
        },
        methods: {
            deleteCourse(index, id) {
                this.courses.splice(index, 1);
                axios.delete(`/courses/api/delete/${id}`).catch(e => {
                    this.errors.push(e)
                });
                this.getAllCourses()
            },
            getEdit(id) {
                return "/courses/edit/" + id;
            },
            getAllCourses() {
                axios.get(`/courses/api`)
                    .then(response => {
                        this.news = response.data;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        }
    }
</script>

<style scoped>

</style>



