<template>
    <div><p class="headline primary--text">Редактировать</p>

        <form class="mt-3">
            <v-text-field
                    v-model="userDto.name"
                    label="Имя">
            </v-text-field>
            <v-text-field
                    v-model="userDto.surname"
                    label="Фамилия">
            </v-text-field>
            <v-text-field
                    input="email"
                    v-model="userDto.email"
                    label="Email">
            </v-text-field>
            <v-text-field
                    v-model="userDto.login"
                    label="Логин">
            </v-text-field>

            <p>Password</p>
            <vue-password v-model="userDto.password">
                <template slot="password-toggle" scope="props">
                    <button class="VuePassword__Toggle white--text primary"
                            type="button"
                            v-on:click="props.toggle"
                            v-text="props.type === 'password' ? 'Показать' : 'Скрыть'">
                    </button>
                </template>
            </vue-password>


            <v-btn class="mt-5 right" color="primary" @click="submit">Сохранить</v-btn>
            <v-btn class="mt-5 right" color="primary" to="/me">Отмена</v-btn>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'
    import VuePassword from "vue-password/src/components/VuePassword";

    export default {
        components: {VuePassword},
        data: () => ({
            userDto: {
                name: 'name',
                surname: 'surname',
                login: 'login',
                id: null,
                role: 'role',
                password: 'password',
                email: 'email'
            },
        }),

        methods: {
            submit() {
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                };
                axios.post(`/users/api/add`, this.userDto, headers)
                    .then(response => {
                        this.userDto = response.data;
                        if (this.newsDto.id != null) {
                            this.$router.push('/me');
                        }
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        }, created() {
            axios.get(`/users/api`)
                .then(response => {
                    this.userDt = response.data
                })
                .catch(e => {
                    this.errors.push(e)
                })
        }
    }
</script>

