<template>
    <div id="news">

        <v-btn color="blue darken-3" to="/news/add" class="ml-3" dark>Добавить</v-btn>
        <v-btn color="blue darken-3" @click="getAllNews(currentPage)" class="ml-3 right" dark>Обновить</v-btn>


        <!--News list-->
        <v-card class="mt-3 ml-3">
            <v-list three-line v-for="(item, index) in users" :key="index">
                <template>
                    <v-list-tile>

                        <v-list-tile-content class="mt-0">
                            <v-list-tile-title>{{item.name}} {{item.surname}}</v-list-tile-title>
                            <v-list-tile-sub-title>{{item.login}}</v-list-tile-sub-title>
                            <v-list-tile-sub-title>Email: {{item.email}}</v-list-tile-sub-title>
                        </v-list-tile-content>

                        <v-list-tile-avatar class="mt-0 right">
                            <v-layout column>
                                <v-flex xs12 sm3>
                                    <v-btn @click="deleteUsers(index, item.id)" flat icon color="grey darken-4">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                </v-flex>
                                <v-flex xs12 sm3>
                                    <v-btn v-bind:to="getEdit(item.id)" flat icon color="blue darken-2">
                                        <v-icon>edit</v-icon>
                                    </v-btn>
                                </v-flex>
                            </v-layout>
                        </v-list-tile-avatar>


                    </v-list-tile>
                    <v-divider class="mt-0"/>
                </template>
            </v-list>


            <v-layout class="right mt-3" row>
                <v-btn @click="previousPage" icon color="primary">
                    <v-icon>navigate_before</v-icon>
                </v-btn>

                <h1 class="mt-0 ml-3 mr-3">{{currentPage + 1}}</h1>
                <v-btn @click="nextPage" icon color="primary">
                    <v-icon>navigate_next</v-icon>
                </v-btn>
            </v-layout>


        </v-card>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";
    import axios from 'axios';
    import VPaginator from "vuejs-paginator-axios/src/VPaginator";
    import AddNews from "./NewsAdd";

    export default {
        components: {AddNews, VPaginator, Icon},
        data() {
            return {
                removeFilters: true,
                users: [
                    {
                        id: 1,
                        name: 'tgest',
                        surname: "fsgd",
                        email: "email",
                        login: "login"
                    },
                    {
                        id: 2,
                        name: 'tgfsest',
                        surname: "fsgd",
                        email: "email",
                        login: "login"
                    },
                    {
                        id: 3,
                        name: 'tgest',
                        surname: "fsgd",
                        email: "email",
                        login: "login"
                    }
                ],

                currentPage: 0,
            }

        },
        created() {
            this.refreshUsers()
        },
        methods: {
            deleteUsers(index, id) {
                this.users.splice(index, 1);
                axios.delete(`/users/api/delete/${id}`).catch(e => {
                    this.errors.push(e)
                });
                this.getAllUsers(this.currentPage)
            },

            getEdit(id) {
                return "/users/edit/" + id;
            },

            getAllUsers(page) {
                axios.get(`/users/api/${page}`)
                    .then(response => {
                        this.news = response.data;
                        this.removeFilters = true;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            },
            refreshUsers() {
                this.currentPage = 0;
                this.getAllUsers(this.currentPage);
            },
            previousPage() {
                if (this.currentPage != 0) {
                    this.currentPage--;
                    this.getAllUsers(this.currentPage);
                }
            },
            nextPage() {
                this.currentPage++;
                this.getAllUsers(this.currentPage);
            }

        }
    }

</script>
<style>

</style>




