import Vue from 'vue'
import Router from 'vue-router'
import News from './components/News'
import Courses from './components/Courses'
import Me from './components/Me'
import User from './components/User'
import AddNews from './components/NewsAdd'
import EditNews from './components/NewsEdit'
import DeletedNews from './components/NewsDeleted'
import AddUser from './components/UserAdd'
import EditUser from './components/UserEdit'
import AddCourse from './components/CourseAdd'
import EditCourse from './components/CourseEdit'

Vue.use(Router);
export default new Router({
    routes: [
        // {
        //     path: '/login',
        //     name: 'login',
        //     component: Login
        // },
        {
            path: '/me',
            name: 'me',
            component: Me
        },
        {
            path: '/news',
            name: 'news',
            component: News
        },
        {
            path: '/courses',
            name: 'courses',
            component: Courses
        },
        {
            path: '/users',
            name: 'user',
            component: User
        },
        {
            path: '/news/edit/:id',
            name: 'news edit',
            component: EditNews,
            props: true
        },
        {
            path: '/news/add',
            name: 'news add',
            component: AddNews
        },
        {
            path: "/",
            redirect: {
                name: "news"
            }
        },
        {
            path: "/deleted",
            name: "deleted news",
            component: DeletedNews
        },
        {
            path: '/courses/add',
            name: 'courses add',
            component: AddCourse
        },
        {
            path: '/courses/edit/:id',
            name: 'courses edit',
            component: EditCourse
        },
        {
            path: '/users/edit/:id',
            name: 'user edit',
            component: EditUser
        },
        {
            path: '/users/add',
            name: 'users add',
            component: AddUser
        },
        {
            path:'/me/edit',
            name:'me edit',
            component:EditUser
        }
    ]
})


